
.header-section {
  background-color: var(--black);
  padding-top: 29px;  
  transition: all .10s;
}
@media screen and (min-width: 1158px) {
  .header-section {
    padding: 20px 0;
  }
}
.scrolled{
  padding-top: 90px;
}
.scrolled #header {
  z-index: 9999;
  background-color: var(--black);
  transition: all var(--action);
  position: fixed;
  top: 0;
  width: 100%;
}
.container-header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .container-header-section {
    padding: 0 32px;
  }
}

/* Header Nav */

.header-nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .header-nav {
    column-gap: 80px;
  }
}

/* Logo */

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  column-gap: 4px;
}
.logo img {
  width: 14px;
  height: 14px;
}
@media screen and (min-width: 768px) {
  .logo img {
    width: 19px;
    height: 19px;
  }
}
.logo-name {
  color: var(--white);
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.36px;
}
@media screen and (min-width: 768px) {
  .logo-name {
    font-size: 15px;
    letter-spacing: -0.45px;
  }
}

/* Header Nav Menu */

.header-navmenu {
  display: none;
}
@media screen and (min-width: 768px) {
  .header-navmenu {
    display: flex;
    column-gap: 40px;
    align-items: center;
  }
}
.header-navmenu-item {
  color: var(--white);
  text-align: center;
  font-family: Epilogue;
  font-size: 9px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33em;
  text-transform: uppercase;
}

/* Header Address */

.address {
  display: none;
  transition: width var(--action) var(--cubic);
}
.address:hover {
  border-color: var(--white);
}
@media screen and (min-width: 768px) {
  .address {
    display: flex;
    padding: 18px 36px;
    border-radius: 8px;
    border: 1px solid var(--dark-green);
    background: var(--dark-green);

    color: var(--white);
    text-align: center;
    font-family: Epilogue;
    font-size: 9px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.33em;
    text-transform: uppercase;
    cursor: pointer;
  }
}

/* Mobile Nav Menu */

.button-navmenu {
  background-color: transparent;
  border: 1px solid var(--black);
}
@media screen and (min-width: 768px) {
  .button-navmenu {
    display: none;
  }
}
.icon-menu-burger {
  width: 24px;
  height: 24px;
  fill: var(--white);
}